<template>
  <section class="playlist">
    <div
      class="head-img"
      :style="{ background: 'url(' + detail.coverImgUrl + ')' }"
    >
      <div class="leftpic">
        <img :src="detail.coverImgUrl" alt="" />
        <span class="playcount">{{ detail.playCount | parsePalyCount }}</span>
        <p class="rightname">{{ detail.name }}</p>
      </div>
    </div>
    <ul v-if="detail">
      <MusicListItem
        @change-current-play-song="$emit('change-current-play-song', $event)"
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        :index="index"
      />
    </ul>

    <ul>
      <CommentCard
        v-for="item in comments"
        :key="item.commentId"
        :item="item"
      />
    </ul>
  </section>
</template>

<script>
import MusicListItem from "../components/MusicListItem.vue";
import CommentCard from "../components/CommentCard.vue";
export default {
  components: {
    MusicListItem,
    CommentCard,
  },
  data: function () {
    return {
      detail: Object,
      comments: null,
      item: Object,
    };
  },
  filters: {
    parsePalyCount: function (value) {
      return value >= 10000
        ? parseFloat((value / 10000).toFixed(1)) + "万"
        : value;
    },
  },
  created() {
    //   获取歌单详情
    this.axios
      .get("https://music.kele8.cn/playlist/detail?id=" + this.$route.query.id)
      .then((res) => {
        this.detail = res.data.playlist;
      });

    // 获取歌单评论
    this.axios
      .get("https://music.kele8.cn/comment/playlist", {
        params: {
          id: this.$route.query.id,
        },
      })
      .then((res) => {
        this.comments = res.data.hotComments;
      });
  },
};
</script>

<style lang="less" >
.playlist {
  position: relative;
  
  .head-img {
    display: flex;
    width: 100%;
    height: 210px;
    padding-bottom: 15px;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 210px;
      background: rgba(0, 0, 0, 0.6);
      position: absolute;
      top: 0;
       
    }

    .leftpic {
      img {
        width: 120px;
        height: 120px;
        margin: 30px;
        position: relative;
        z-index: 1;
      }
      .playcount {
        position: absolute;
        top: 36px;
        left: 90px;
        font-size: 10px;
        color: gray;
      }
    }
    .rightname {
      font-size: 12px;
     position: absolute;
      top: 160px;
      right: 60px;
      color: white;
      z-index: 1;
    }
  }
}
</style>