<template>
  <section class="recommend">
    <RecommendTitle>推荐歌单</RecommendTitle>
     
    <ul class="card-row3">
      <PlayListCard
        class="card"
        v-for="item in personalized.slice(0, 6)"
        :key="item.id"
        :item="item"
      />
    </ul>

    <RecommendTitle>最新音乐</RecommendTitle>

    <ul>
      <MusicListItem
        v-for="item in newsongs"
        :key="item.id"
        :item="item"
        @change-current-play-song="
          $emit('change-current-play-song', $event);
          $emit('change-current-play-song-list', newsongs);
        "
      />
    </ul>
  </section>
</template>

<script>
// @ 代表 src
import RecommendTitle from "@/components/RecommendTitle.vue";
import PlayListCard from "@/components/PlayListCard.vue";
import MusicListItem from "@/components/MusicListItem.vue";
export default {
  data: function () {
    return {
      personalized: [],
      newsongs: [],
    };
  },
  components: {
    RecommendTitle,
    PlayListCard,
    MusicListItem,
  },
  created() {
    // console.log(this);
    // 获取推荐歌单
    this.axios
      .get("https://music.kele8.cn/personalized")
      .then((response) => {
        // console.log(response.data.result, this);
        this.personalized = response.data.result;
      })
      .catch((error) => {
        console.log(error);
      });

    // /personalized/newsong
    this.axios
      .get("https://music.kele8.cn/personalized/newsong")
      .then((response) => {
        // console.log(response.data.result, this);
        this.newsongs = response.data.result;
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>

<style lang="less">
.recommend {
  // margin-bottom: 50px;
  .card-row3 {
    display: flex;
    flex-wrap: wrap;
    .card {
      flex: 1 0 30%;
      &:nth-child(3n + 2) {
        // background: red;
        margin: 0 3px;
      }
    }
  }
}
