<template>
  <section class="palypage-lyric">
    <ul
      ref="lyricBox"
      v-if="parsedLyric"
      :style="{ marginTop: -offsetTop + 'px' }"
    >
      <li
        v-for="(item, index) in parsedLyric"
        :key="index"
        :class="{ active: index == currentLyricIndex, paused: !playing }"
      >
        <span
          :style="{
            animationDuration:
              index >= parsedLyric.length - 1
                ? (duration - item.time) / 2 + 's'
                : parsedLyric[index + 1].time - item.time + 's',
          }"
          >{{ item.text }}</span
        >
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  props: {
    currentLyricIndex: Number,
    parsedLyric: Array,
    playing: Boolean,
    duration: Number,
  },
  data: function () {
    return {
      offsetTop: 0,
    };
  },

  mounted() {
    // console.log(this.$refs.lyricBox.querySelectorAll("li"));
  },
  watch: {
    currentLyricIndex: function () {
      var h = 0;
      this.$refs.lyricBox.querySelectorAll("li").forEach((element, index) => {
        if (index < this.currentLyricIndex) {
          h += element.offsetHeight;
        }
      });
      console.log(h);
      this.offsetTop = h;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes mymove {
  from {
    background-position-x: -100%;
  }
  to {
    background-position-x: -200%;
  }
}
.palypage-lyric {
  flex: 1;
  overflow: hidden;
  margin: 30px;
  ul {
    transform: translateY(244px);
    transition: all 0.5s;
    li {
      text-align: center;
      color: rgb(226, 226, 226);
      span {
        line-height: 2em;
      }
      &.active {
        span {
          background: linear-gradient(to right, #52ef57 50%, #e322ef 55%);
          background-clip: text;
          -webkit-text-fill-color: transparent;
          background-size: 200%;
          background-position-x: -100%;

          animation: mymove linear;
        }
        &.paused {
          span {
            animation-play-state: paused;
          }
        }
      }
    }
  }
}
</style>