<template>
  <li
    class="current-play-list-item"
    @click="$emit('change-current-play-song', item)"
  >
    <div
      class="palying-animate"
      :class="{ paused: !playing }"
      v-if="currentPlaySong.id == item.id"
    >
      <span></span>
      <span></span>
      <span></span>
    </div>
    <div class="detail">
      <span>{{ item.name }} - </span>

      <span class="artist" v-for="artist in item.song.artists" :key="artist.id">
        {{ artist.name }}
      </span>
    </div>

    <div class="delete"><img src="../assets/错号.png" alt=""></div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    currentPlaySong: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less">
li.current-play-list-item {
  display: flex;
    margin-left: 15px;
margin: 15px;
  @keyframes shensuo {
    from {
      height: 20%;
    }
    to {
      height: 100%;
    }
  }
  .palying-animate {
    width: 20px;
    height: 20px;
    // background: green;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
   
    span {
      width: 20%;
      height: 20%;
      background: red;
      animation: shensuo 0.6s linear infinite alternate;
      // margin-right: 8px;
      &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: -0.2s;
      }
      &:nth-child(3) {
        animation-delay: -0.4s;
      }
    }
    &.paused {
