<template>
  <section class="playbar">
    <img class="radius"
      :src="`${
        currentPlaySong.picUrl || currentPlaySong.al.picUrl
      }?imageView=1&type=webp&thumbnail=60x0`"
      alt=""
    />
    <h5>{{ currentPlaySong.name }}</h5>
    <div class="progress">
      <span
        v-if="!playing"
        class="play-icon"
        @click.stop="$emit('audio-play')"
      ></span>
      <span v-else class="paused-icon" @click.stop="$emit('audio-pause')"
        ><img src="../assets/yes1.png" alt=""></span
      >
      <canvas ref="canvas" width="40" height="40"></canvas>
    </div>
    <div class="currentlist" @click.stop="$emit('show-list')"><img class="playlist" src="../assets/songlist.png" alt=""></div>
  </section>
</template>

<script>
export default {
  props: {
    currentPlaySong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    circlePercent: Number,
  },

  methods: {
    drawCircle: function (n) {
      var canvas = this.$refs.canvas;
      const ctx = canvas.getContext("2d");
      // ctx.fillStyle = "green";

      ctx.clearRect(0, 0, 40, 40);
      // ctx.fillRect(0, 0, 40, 40);

      // ctx.strokeStyle = "#ddd";

      // ctx.beginPath();
      // ctx.arc(20, 20, 18, 0, 2 * Math.PI);
      // // ctx.closePath();
      // ctx.stroke();

      ctx.strokeStyle = "red";

      ctx.beginPath();
      ctx.arc(20, 20, 18, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI);
      // ctx.closePath();
      ctx.stroke();

      ctx.strokeStyle = "#ccc";

      ctx.beginPath();

      ctx.arc(20, 20, 18, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI, true);
      ctx.stroke();

      // console.log("xxxxxx", this);
    },
  },
  mounted() {
    this.drawCircle(this.circlePercent);
  },
  watch: {
    circlePercent: function (n, o) {
      // console.log(n);
      this.drawCircle(n);
    },
  },
};
</script>

<style lang="less" >
.playbar {
  width: 100%;
  height: 60px;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;

  display: flex;
  padding: 5px 10px;

  align-items: center;
  //   justify-content: space-between;

  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.15);

  .radius {
    border-radius: 50%;
    width: 40px;
    height: 40px;
  }
  h5 {
    flex: 1;
    padding: 0 10px;
  }
  .progress {
    position: relative;
    width: 34px;
    height: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
    margin-right: 17px;
    span {
      &.play-icon {
        width: 15px;
        height: 15px;
        // background: red;
        overflow: hidden;

        &::before {
          content: "";
          width: 30px;
          height: 15px;
          display: block;
          // background: green;
          border-top: 7.5px solid transparent;
          border-right: 18px solid transparent;
          border-bottom: 7px solid transparent;
          border-left: 12px solid #ccc;
          margin-left: 4px;
        }
      }
      &.paused-icon{

        overflow: hidden;
      }
    }
.currentlist{
   width: 30px;
 height: 30px;margin-top: 10px;

  .playlist{
   
   
  }
}
    canvas {
      position: absolute;
      top: -2px;
      left:0;
      z-index: -1;
      width: 100%;
      height: 100%;
    }
  }
}
</style>