<template>
  <section class="playpage">
    <div
      class="mask"
      :style="{
        backgroundImage: `url(
        ${
          currentPlaySong.picUrl || currentPlaySong.al.picUrl
        }?imageView=1&type=webp&thumbnail=120x0
      )`,
      }"
    ></div>
    <PlayPageHeader
      :currentPlaySong="currentPlaySong"
      @update:showBar="$emit('update:showBar', $event)"
      @chang-showDisc="changeShowDisc"
    />
    <PlayPageDisc
      v-if="showDisc"
      :currentPlaySong="currentPlaySong"
      :playing="playing"
    />
    <PlayPageLyric
      v-else
      :parsedLyric="parsedLyric"
      :currentLyricIndex="currentLyricIndex"
      :playing="playing"
      :duration="duration"
    />

    <PlayPageControl
      :duration="duration"
      :currentTime="currentTime"
      @updata:currentTime="$emit('updata:currentTime', $event)"
      :playing="playing"
      @audio-pause="$emit('audio-pause')"
      @audio-play="$emit('audio-play')"
      @show-list="$emit('show-list')"
      @prev-song="$emit('prev-song')"
      @next-song="$emit('next-song')"
    />
  </section>
</template>

<script>
import PlayPageHeader from "../components/PlayPageHeader.vue";
import PlayPageLyric from "../components/PlayPageLyric.vue";
import PlayPageDisc from "../components/PlayPageDisc.vue";
import PlayPageControl from "../components/PlayPageControl.vue";
export default {
  components: {
    PlayPageLyric,
    PlayPageHeader,
    PlayPageDisc,
    PlayPageControl,
  },
  props: {
    currentPlaySong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    circlePercent: Number,
    currentLyricIndex: Number,
    parsedLyric: Array,
  },
  data: function () {
    return {
      showDisc: false,
    };
  },
  methods:{
    changeShowDisc:function(){
       this.showDisc = !this.showDisc
    }
  }
};
</script>

<style lang="less">
.playpage {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.35);
  position: fixed;
  bottom: 0;
  left: 0;
  z-index:999 ;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    filter: blur(30px) brightness(0.5);
    transform: scale(1.5);
    z-index: -1;
  }
}
</style>