<template>
  <section class="disc" :class="{ paused: !playing }">
    <img
      :src="`${
        currentPlaySong.picUrl || currentPlaySong.al.picUrl
      }?imageView=1&type=webp&thumbnail=240x0`"
      alt=""
    />
  </section>
</template>

<script>
export default {
  props: {
    currentPlaySong: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less">
@keyframes disc-rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.disc {
  img {
    display: block;
    border-radius: 50%;
    animation: disc-rotate 10s linear infinite;
    margin: 0 auto;
  }
  &.paused {
    img {
      animation-play-state: paused;
    }
  }
}
</style>