<template>

  <li class="playlist-card" @click="$router.push('/playlist?id=' + item.id)">
   
    <div class="thumb">
      <img
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=246x0`"
        alt=""
      />
      <span>{{ item.playCount | parsePalyCount }}</span>
    </div>
    <h5>{{ item.name }}</h5>
    
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  filters: {
    parsePalyCount: function (value) {
      return value >= 10000
        ? parseFloat((value / 10000).toFixed(1)) + "万"
        : value;
    },
  },
};
</script>

<style lang="less">
li {
  &.playlist-card {
    .thumb {
      position: relative;
      top: 0;
      left: 0;
      //   font-size: 0;
      img {
        width: 100%;
        // display: block;
        vertical-align: middle;
      }
      span {
        position: absolute;
        top: 5px;
        right: 5px;
        color: white;
        font-size: 12px;
        vertical-align: middle;
        line-height: 1em;
        &::before {
          content: "";
          width: 1em;
          height: 1em;
          display: inline-block;
          background: url("data:image/svg+xml;base64,");
          background-repeat: no-repeat;
          vertical-align: bottom;
          margin-right: 3px;
        }
      }
    }
    h5 {
      margin: 5px 0 10px 0;
      font-size: 13px;
      padding: 0 5px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-align: justify;
      word-break: break-all;
      line-height: 1.2em;
    }
  }
}
</style>