<template>
  <section class="paly">
    <audio
      style="height: 40px; position: fixed; top: 0; left: 0"
      :src="currentPlaySong.id | id2url"
      autoplay
      ref="audio"
      @play="playing = true"
      @pause="playing = false"
      @durationchange="durationchange"
      @timeupdate="timeupdate"
      @ended="$emit('next-song')"
    ></audio>

    <!-- <div v-if="showBar" class="playbar">playbar</div>
    <div v-else class="playpage">playpage</div> -->

    <PlayBar
      :currentPlaySong="currentPlaySong"
      v-if="showBar"
      :playing="playing"
      :duration="duration"
      :currentTime="currentTime"
      :circlePercent="circlePercent"
      @audio-play="audioPlay"
      @audio-pause="audioPause"
      @click.native="showBar = false"
      @show-list="showList"
    />
    <PlayPage
      v-else
      :currentPlaySong="currentPlaySong"
      :playing="playing"
      :duration="duration"
      :currentTime="currentTime"
      :circlePercent="circlePercent"
      @update:showBar="showBar = $event"
      @updata:currentTime="$refs.audio.currentTime = $event"
      @audio-play="audioPlay"
      @audio-pause="audioPause"
      @show-list="showList"
      :parsedLyric="parsedLyric"
      :currentLyricIndex="currentLyricIndex"
      @prev-song="$emit('prev-song')"
      @next-song="$emit('next-song')"
    />

    <Transition
      name="custom-classes-transition"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <CurrentPlayList
        v-if="isShowList"
        @hide-list="hideList"
        :currentPlaySongList="currentPlaySongList"
        :currentPlaySong="currentPlaySong"
        :playing="playing"
        @change-current-play-song="$emit('change-current-play-song', $event)"
      />
    </Transition>
  </section>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import PlayPage from "@/components/PlayPage.vue";
import CurrentPlayList from "@/components/CurrentPlayList.vue";
export default {
  components: {
    PlayBar,
    PlayPage,
    CurrentPlayList,
  },
  props: {
    currentPlaySong: Object,
    currentPlaySongList: Array,
  },
  data: function () {
    return {
      showBar: true,
      isShowList: false,
      playing: null,
      duration: 0,
      currentTime: 0,
      lyricStr: "",
    };
  },
  methods: {
    durationchange: function () {
      //   console.log('总时长',this.$refs.audio.duration);
      this.duration = this.$refs.audio.duration;
    },
    timeupdate: function () {
      //   console.log('现时间',this.$refs.audio.currentTime);
      this.currentTime = this.$refs.audio.currentTime;
    },
    audioPlay: function () {
      this.$refs.audio.play();
    },
    audioPause: function () {
      this.$refs.audio.pause();
    },
    showList: function () {
      this.isShowList = true;
    },
    hideList: function () {
      this.isShowList = false;
    },
  },
  computed: {
    circlePercent: function () {
      return this.duration ? this.currentTime / this.duration : 0;
    },
    parsedLyric: function () {
      return this.lyricStr
        ? this.lyricStr
            .split(/\n/gim)
            .filter((e) => e)
            .map((e) => {
              var x = e.split(/\]/);
              var m = x[0].replace("[", "").split(":");

              return {
                time: (m[0] * 60 + m[1] * 1).toFixed(3),
                text: x[1],
              };
            })
        : [];
    },
    currentLyricIndex: function () {
      var index = this.parsedLyric.findIndex((e) => e.time > this.currentTime);
      return index == -1 ? this.parsedLyric.length - 1 : index - 1;
    },
  },

  //   created() {
  //     console.log(this.$refs);
  //   },
  filters: {
    id2url: function (id) {
      return "https://music.163.com/song/media/outer/url?id=" + id + ".mp3";
    },
  },

  created() {
    this.axios
      .get("https://music.kele8.cn/lyric", {
        params: {
          id: this.currentPlaySong.id,
        },
      })
      .then((res) => {
        // console.log(res.data.lrc.lyric);
        this.lyricStr = res.data.lrc.lyric;
      });
  },

  watch: {
    currentPlaySong: function (n) {
      console.log("歌曲切换了");

      // 先把上一首歌曲的 歌词 清除
      this.lyricStr = "";

      this.axios
        .get("https://music.kele8.cn/lyric", {
          params: {
            id: n.id,
          },
        })
        .then((res) => {
          // console.log(res.data.lrc.lyric);
          this.lyricStr = res.data.lrc.lyric;
        });
    },
    parsedLyric: function (n, o) {
      console.log("parsedLyric变化了", n, o);
    },
  },
};
</script>

<style lang="less">
.animate__animated {
  animation-duration: 0.3s;
}
</style>