<template>
  <footer class="control">
    <div class="progerss">
      <input
        type="range"
        min="0"
        :max="duration"
        @input="inputing = true"
        @change="changeValue"
        v-model="value"
      />
      <div class="line"></div>
      <div
        class="dot"
        :style="{
          left: ((inputing ? value : currentTime) / duration) * 100 + '%',
        }"
      ></div>
    </div>

    <ul class="icons">
      <li class="skin"><img src="../assets/皮肤切换.png" alt=""></li>
      <li @click="$emit('prev-song')"><img src="../assets/上二.png" alt=""></li>
      <li>
        <span v-if="!playing" @click.stop="$emit('audio-play')"><img src="../assets/stop.png" alt=""></span>
        <span v-else @click.stop="$emit('audio-pause')"><img src="../assets/启动.png" alt=""></span>
      </li>
      <li @click="$emit('next-song')"><img src="../assets/下二.png" alt=""></li>
      <li @click="$emit('show-list')"><img src="../assets/歌曲列表.png" alt=""></li>
    </ul>
  </footer>
</template>

<script>
export default {
  props: {
    duration: Number,
    currentTime: Number,
    playing: Boolean,
  },
  data: function () {
    return {
      inputing: false,
      value: this.currentTime,
    };
  },
  methods: {
    changeValue: function (e) {
      this.inputing = false;
      //   console.log(e.target.value);
      this.$emit("updata:currentTime", e.target.value);
    },
  },
};
</script>

<style lang="less" scoped>
.control {
  // position: absolute;
  // bottom: 0;
  // left: 0;
  // width: 100%;
  .progerss {
    width: 100%;
    height: 10px;
    // background: lightcoral;

    position: relative;
    display: flex;
    align-items: center;

    input {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      z-index: 1;
      opacity: 0;
    }
    .line {
      background: white;
      width: 100%;
      height: 4px;
      border-radius: 10px;
    }
    .dot {
      width: 10px;
      height: 10px;
      background: white;
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
      margin-left: -5px;
      z-index: 0;
      box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
    }
  }
  .icons {
    margin:50px 0;
    display: flex;
    justify-content: space-around;
  }
}
</style>